.skills__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.skills__container > div {
    background: #005A00;
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.skills__container > div:hover{
    background: transparent;
    border-color:#005A00;
    cursor: default;
}

.skills__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: #1EFF6A;
}

.skills__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.skills__details{
    display: flex;
    gap: 1rem;
}

.skills__details-icon{
    margin-top: 6px;
    color: #1EFF6A;
}


/* MEDIA QUERIES  (MEDIUM DEVICES) */

@media screen and (max-width: 1024px){
    .skills__container{
        grid-template-columns: 1fr;
    }

    .skills__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .skills__content{
        padding: 1rem;
    }
}

/* MEDIA QUERIES  (SMALL DEVICES) */

@media screen and (max-width: 600px){
    .skills__container{
        gap: 1rem;
    }

    .skills__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}