

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
   --color-bg: black;
   --container-width-lg: 75%;
   --container-width-md: 86%;
   --container-width-sm: 90%;
   --transition: all 400ms ease;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Comfortaa', cursive;
    background: #000000;
    color: #0eff32;
    line-height: 1.7;
    margin: 0;
    /* overflow: hidden; */
    display: block;
}

/* General Styles */

.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5{
    text-align: center;
    color: #969796;
}

section > h2 {
    color: #1EFF6A;
    margin-bottom: 3rem;
}

.text-light {
    color:#cfcfcf;
}

a{
    color: #1EFF6A;
    transition: var(--transition);
}

a:hover{
    color: #009900;
}

.btn{
    width: max-content;
    display: inline-block;
    color: #1EFF6A;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #1EFF6A;
    transition: var(--transition);
}

.btn:hover{
    background: #009900;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #1EFF6A;
    color: var(--color-bg);
}

.img{
    display: block;
    width: 100%;
    object-fit: cover;
}


/* MEDIA QUERIES  (MEDIUM DEVICES) */

@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/* MEDIA QUERIES  (SMALL DEVICES) */

@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}