nav{
    background: rgba(72, 71, 71, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color:#3D4A3D;
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(122, 120, 120, 0.3);
}

nav a.active{
    background: #1EFF6A;
    color: black;
}